import QPagination from '~/components/QPagination'
import FloatButtonGroup from '~/components/FloatButtonGroup'
const limit = 50
// function loadData($axios, p) {
//   return $axios
//     .post(`/getMessage`, {
//       limit,
//       offset: (p - 1) * limit
//     })
//     .then((data) => {
//       data.page = p
//       return data
//     }).catch((err) => {
//       console.log(err)
//       return { page: p }
//     })
// }
export default {
  components: {
    QPagination, FloatButtonGroup
  },
  validate({ params }) {
    // 必须是number类型
    return params.p == null || /^\d+$/.test(params.p)
  },
  asyncData({ params, $axios, error }) {
    const p = parseInt(params.p || 1)
    return $axios
      .post(`/getMessage`, {
        limit,
        offset: (p - 1) * limit
      })
      .then((data) => {
        data.page = p
        return data
      }).catch((err) => {
        console.log(err)
        if (err.response.status === 200) {
          const data = err.response.data
          if (data.code === 9 || data.code === 10) {
            return { page: p, dialog: true }
          }
        }
        error({ statusCode: 500, message: 'An error occurred in the application and your page could not be served. If you are the application owner, check your logs for details.' })
        // redirect(`/login?redirect=/message`)
      })
    // return loadData($axios, p)
  },
  data() {
    return {
      msg: '',
      msgMaxLength: 500,
      rules: [v => v.length <= this.msgMaxLength || `最大字数不能超过 ${this.msgMaxLength}`],
      messages: [],
      fmt: '/message/:p',
      errMsg: undefined,
      snackbar: false,
      status: '',
      text: '',
      timeout: 3000,
      count: 0,
      dialog: false
    }
  },
  computed: {
    total() {
      return Math.ceil(this.count / limit)
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    sendMessage() {
      if (!this.msg || !this.msg.trim()) {
        return
      }
      this.$axios.post('/sendMessage', {
        fromContent: this.msg
      }).then((res) => {
        this.text = '成功'
        this.status = 'success'
        this.snackbar = true
        const p = Math.ceil((this.count + 1) / limit)
        if (p === this.page) {
          // loadData(this.$axios, p).then((data) => {
          //   Object.assign(this.$data, data)
          // })
          this.messages.push({
            fromContent: this.msg,
            fromTime: '刚刚',
            fromUserName: this.$store.name
          })
        } else {
          this.$router.push(`/message/${p}`)
        }
        this.msg = ''
      }).catch((err) => {
        this.text = err.response.data.resMsg || '发送留言失败，请重试'
        this.status = 'error'
        this.snackbar = true
        console.log(err)
      })
    },
    onTextareaChange(val) {
      if (val.length > this.msgMaxLength) {
        this.msg = val.slice(0, this.msgMaxLength)
      }
    }
  }
}
